var LoanHeader = React.createClass({
    render: function () {
        return (
            <h1 className="page-header">BeregnBoliglån.dk</h1>
        );
    }
});

var LoanInformation = React.createClass({
    hasAllInformationBeenGiven: function (loanInformation) {
        return loanInformation.company &&
            loanInformation.amount &&
            loanInformation.value &&
            loanInformation.numberOfLoans &&
            loanInformation.value >= loanInformation.amount;
    },
    onInformationChange: function (e) {
        e.preventDefault();
        var loanInformation = {
            company: React.findDOMNode(this.refs.companySelection).options[this.refs.companySelection.getDOMNode().selectedIndex].disabled ? undefined : React.findDOMNode(this.refs.companySelection).options[this.refs.companySelection.getDOMNode().selectedIndex].value,
            amount: parseInt(React.findDOMNode(this.refs.loanAmount).value),
            value: parseInt(React.findDOMNode(this.refs.valueOfHouse).value),
            numberOfLoans: React.findDOMNode(this.refs.numberOfLoans).options[this.refs.numberOfLoans.getDOMNode().selectedIndex].value,
            hasAllInformationBeenGiven: false
        };
        loanInformation.hasAllInformationBeenGiven = this.hasAllInformationBeenGiven(loanInformation);

        this.props.onInformationChange(loanInformation);
    },
    render: function () {
        var companySelectionOptions = [];
        this.props.loanConfiguration.companies.forEach(function (company) {
            companySelectionOptions.push(<option value={company.id }>{company.name}</option>);
        });

        return (
        <div className="row" id="loanInformation">
            <div className="col-lg-8 col-md-10 col-md-offset-1 information-box">
                <div className="row">
                    <div className="col-md-offset-1 col-sm-11">
                        <h4>Informationer om lån</h4>
                    </div>
                </div>
                <div className="row">
                    <div className="form-group col-sm-4">
                        <label for="companySelection">Selskab</label>
                        <select id="companySelection" onChange={this.onInformationChange} className="form-control" ref="companySelection">
                            <option disabled selected> -- Vælg selskab -- </option>
                            {companySelectionOptions}
                        </select>
                    </div>
                    <div className="form-group col-sm-3">
                        <label for="loanAmount">Lånebeløb</label>
                        <div className="input-group">
                            <input type="number"
                                   min="100000"
                                   step="50000"
                                   onChange={this.onInformationChange}
                                   className="form-control text-right"
                                   id="loanAmount"
                                   placeholder="1.500.000"
                                   ref="loanAmount" />
                            <span className="input-group-addon">kr.</span>
                        </div>
                    </div>
                    <div className="form-group col-sm-3">
                        <label for="valueOfHouse">Værdi af bolig</label>
                        <div className="input-group">
                            <input type="number" min="100000" step="50000" onChange={this.onInformationChange} className="form-control text-right" id="valueOfHouse" placeholder="2.000.000" ref="valueOfHouse" />
                            <span className="input-group-addon">kr.</span>
                        </div>
                    </div>
                    <div className="form-group col-sm-2">
                        <label for="numberOfLoans">Antal lån</label>
                        <select id="numberOfLoans" className="form-control" onChange={this.onInformationChange} ref="numberOfLoans">
                            <option>1</option>
                            <option>2</option>
                            <option>3</option>
                        </select>
                    </div>
                </div>
            </div>
        </div>
        );
    }
});

var LoanInput = React.createClass({
    numberToStringConversion: function (number) {
        switch (number) {
            case 1:
                return "Første";
            case 2:
                return "Andet";
            case 3:
                return "Tredje";
            default:
                throw new Exception();
        }
    },
    calculateLoanSplitPercentage: function (loanNumber, numberOfLoans) {
        var splitPercentage = 100 / numberOfLoans;
        if (loanNumber != numberOfLoans) {
            return Math.floor(splitPercentage);
        } else {
            var splitPercentageDecimals = splitPercentage % 1;
            return Math.round(splitPercentage + ((numberOfLoans - 1) * splitPercentageDecimals));
        }
    },
    componentWillUpdate: function (nextProps, nextState) {
        var nextNumberOfLoans = nextProps.loanInformation.numberOfLoans;
        var previousNumberOfLoans = this.props.loanSpecification.length;

        // Do nothing if the same number of loans are selected and same company
        if (nextNumberOfLoans == previousNumberOfLoans && nextProps.loanInformation.company == this.props.loanInformation.company)
            return;
        if (nextProps.loanInformation.company == undefined)
            return;

        var selectedCompany = nextProps.loanInformation.company;
        var selectedCompanyDefaultSettings = undefined;
        for (var index = 0; index < this.props.loanConfiguration.defaultSettings.length; index++) {
            var companyDefaultSettings = this.props.loanConfiguration.defaultSettings[index];
            if (selectedCompany == companyDefaultSettings.companyId)
                selectedCompanyDefaultSettings = companyDefaultSettings;
        }

        var specification = [];
        for (var loanIndex = 0; loanIndex < nextNumberOfLoans; loanIndex++) {
            var type = selectedCompanyDefaultSettings.settings[loanIndex].loanTypeId;
            var period = selectedCompanyDefaultSettings.settings[loanIndex].loanPeriodId;
            var interestRate = selectedCompanyDefaultSettings.settings[loanIndex].loanInterestRateId;

            // If the specification for the given loan index existed in the previous state,
            // then reuse the information from that specification, so that the user does not
            // need to reinput this information
            if (this.props.loanSpecification.length > loanIndex) {
                type = this.props.loanSpecification[0].type;
                period = this.props.loanSpecification[0].period;
                interestRate = this.props.loanSpecification[0].interestRate;
            }

            specification.push({
                percentage: this.calculateLoanSplitPercentage(loanIndex + 1, nextNumberOfLoans),
                type: type,
                period: period,
                interestRate: interestRate,
            });
        }

        this.props.onSpecificationChange(specification);
    },
    correctPercentagesToATotalOfOneHundred: function (oldSpecification, newSpecification) {
        var percentageSum = 0;
        newSpecification.forEach(function (spec) { percentageSum += spec.percentage });

        if (percentageSum === 100)
            return;

        var diffPercentage = 100 - percentageSum;
        if (newSpecification[newSpecification.length - 1].percentage == oldSpecification[oldSpecification.length - 1].percentage) {
            newSpecification[newSpecification.length - 1].percentage += diffPercentage;
        } else {
            newSpecification[0].percentage += diffPercentage;
        }
    },
    onSpecificationChange: function (e) {
        e.preventDefault();
        var loanSpecifications = [];
        for (var loanNumber = 1; loanNumber < this.props.loanSpecification.length + 1; loanNumber++) {
            var loanSpecification = {
                percentage: React.findDOMNode(this.refs["loanPercentage" + loanNumber]).valueAsNumber,
                type: React.findDOMNode(this.refs["loanType" + loanNumber]).options[this.refs["loanType" + loanNumber].getDOMNode().selectedIndex].value,
                period: parseInt(React.findDOMNode(this.refs["loanPeriod" + loanNumber]).options[this.refs["loanPeriod" + loanNumber].getDOMNode().selectedIndex].value),
                interestRate: parseFloat(React.findDOMNode(this.refs["loanInterestRate" + loanNumber]).options[this.refs["loanInterestRate" + loanNumber].getDOMNode().selectedIndex].value)
            };
            loanSpecifications.push(loanSpecification);
        }

        this.correctPercentagesToATotalOfOneHundred(this.props.loanSpecification, loanSpecifications);
        this.props.onSpecificationChange(loanSpecifications);
    },
    render: function () {
        var selectedCompanyConfiguration = undefined;
        for (var index = 0; index < this.props.loanConfiguration.companies.length; index++) {
            if (this.props.loanConfiguration.companies[index].id == this.props.loanInformation.company)
                selectedCompanyConfiguration = this.props.loanConfiguration.companies[index];
        }
        var loanTypeSelectionOptions = [];
        if (selectedCompanyConfiguration != undefined) {
            selectedCompanyConfiguration.loanTypes.forEach(function (loanType) {
                loanTypeSelectionOptions.push(<option value={loanType.id }>{loanType.name}</option>);
            });
            }

        var loanNodes = [];
        var numberOfLoans = this.props.loanSpecification.length;
        for (var loanNumber = 1; loanNumber < numberOfLoans + 1; loanNumber++) {
            var loan = this.props.loanSpecification[loanNumber - 1];

            var selectedLoanType = undefined;
            if (selectedCompanyConfiguration != undefined) {
                for (var index = 0; index < selectedCompanyConfiguration.loanTypes.length; index++) {
                    if (selectedCompanyConfiguration.loanTypes[index].id == loan.type)
                        selectedLoanType = selectedCompanyConfiguration.loanTypes[index];
                }
            }
            var loanPeriodSelectionOptions = [];
            if (selectedLoanType != undefined) {
                selectedLoanType.periods.forEach(function (loanPeriod) {
                    loanPeriodSelectionOptions.push(<option value={loanPeriod.id }>{loanPeriod.period}</option>);
                });
                }

                var selectedLoanPeriod = undefined;
                if (selectedLoanType != undefined) {
                    for (var index = 0; index < selectedLoanType.periods.length; index++) {
                        if (selectedLoanType.periods[index].id == loan.period)
                            selectedLoanPeriod = selectedLoanType.periods[index];
                    }
                }
                var loanInterestRateSelectionOptions = [];
                if (selectedLoanPeriod != undefined) {
                    selectedLoanPeriod.interestRate.forEach(function (interestRate) {
                        loanInterestRateSelectionOptions.push(<option value={interestRate.id }>{interestRate.interestRate}%</option>);
                    });
                    }

                    loanNodes = loanNodes.concat(
            <div className="row" id={"loan" + loanNumber }>
                        <div className={"col-lg-8 col-md-10 col-md-offset-1 information-box " + (this.props.loanInformation.hasAllInformationBeenGiven ? "" : "information-box-disabled") }>
                            <div className="row">
                                <div className="col-md-offset-1 col-sm-11">
                                    <h4>{this.numberToStringConversion(loanNumber)} lån</h4>
                                </div>
                            </div>
                            <div className="row">
                                <div className="form-group col-sm-2 col-xs-5">
                                    <label for={"loanPercentage" + loanNumber }>Procentdel</label>
                                    <div className="input-group">
                                        <input type="number"
                                               value={loan.percentage}
                                               min="0"
                                               max="99"
                                               step="1"
                                               className="form-control text-right"
                                               id={"loanPercentage" + loanNumber}
                                               placeholder="0 - 100"
                                               disabled={ this.props.loanInformation.hasAllInformationBeenGiven ? false : "disabled"}
                                               onChange={this.onSpecificationChange}
                                               ref={"loanPercentage" + loanNumber
                                        } />
                                        <span className="input-group-addon">%</span>
                                    </div>
                                </div>
                                <div className="form-group col-sm-4 col-xs-7">
                                    <label for={"loanType" + loanNumber }>Lånetype</label>
                                    <select type="text"
                                            className="form-control"
                                            value={loan.type}
                                            id={"loanType" + loanNumber}
                                            disabled={ this.props.loanInformation.hasAllInformationBeenGiven ? false : "disabled"}
                                            onChange={this.onSpecificationChange}
                                            ref={"loanType" + loanNumber
                                    }>
                                        {loanTypeSelectionOptions}
                                    </select>
                                </div>
                                <div className="form-group col-sm-3 col-xs-5">
                                    <label for={"loanPeriod" + loanNumber }>Låneperiode</label>
                                    <select type="text"
                                            className="form-control"
                                            value={loan.period}
                                            id={"loanPeriod" + loanNumber}
                                            disabled={ this.props.loanInformation.hasAllInformationBeenGiven ? false : "disabled"}
                                            onChange={this.onSpecificationChange}
                                            ref={"loanPeriod" + loanNumber
                                    }>
                                        {loanPeriodSelectionOptions}
                                    </select>
                                </div>
                                <div className="form-group col-sm-3 col-xs-7">
                                    <label for={"loanInterestRate" + loanNumber }>Rente</label>
                                    <select id={"loanInterestRate" + loanNumber}
                                            className="form-control"
                                            value={loan.interestRate}
                                            disabled={ this.props.loanInformation.hasAllInformationBeenGiven ? false : "disabled"}
                                            onChange={this.onSpecificationChange}
                                            ref={"loanInterestRate" + loanNumber
                                    }>
                                        {loanInterestRateSelectionOptions}
                                    </select>
                                </div>
                            </div>
                        </div>
            </div>
        );
        }

        return (
            <div>
                {loanNodes}
            </div>
        );
    }
});

var LoanResultsOverview = React.createClass({
    render: function () {
        return (
            <div className="row" id="loanOverviewResults">
                <div className={"col-lg-8 col-md-10 col-md-offset-1 information-box " + (this.props.specificationCalculation === undefined ? "information-box-disabled" : "")}>
                    <div className="row">
                        <div className="col-md-offset-1 col-md-11">
                            <h4>Oversigtsresultat</h4>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-sm-3 col-xs-6 text-center">
                            <label>Ydelse 1.år</label>
                            <p>{this.props.specificationCalculation === undefined ? "0" : this.props.specificationCalculation.overview.firstYearPayment.toLocaleString()} kr.</p>
                        </div>
                        <div className="col-sm-3 col-xs-6 text-center">
                            <label>Afdrag 1.år</label>
                            <p>{this.props.specificationCalculation === undefined ? "0" : this.props.specificationCalculation.overview.firstYearRepayment.toLocaleString()} kr.</p>
                        </div>
                        <div className="col-sm-3 col-xs-6 text-center">
                            <label>Rente 1.år</label>
                            <p>{this.props.specificationCalculation === undefined ? "0" : this.props.specificationCalculation.overview.firstYearInterest.toLocaleString()} kr.</p>
                        </div>
                        <div className="col-sm-3 col-xs-6 text-center">
                            <label>Bidrag 1.år</label>
                            <p>{this.props.specificationCalculation === undefined ? "0" : this.props.specificationCalculation.overview.firstYearContribution.toLocaleString()} kr.</p>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
});

var LoanResultsDetailed = React.createClass({
    render: function () {
        var detailedResultRows = [];
        if (this.props.specificationCalculation !== undefined) {
            this.props.specificationCalculation.paymentPlan.forEach(function (paymentYear) {
                detailedResultRows.push(
                    <tr>
                        <td>{paymentYear.year}</td>
                        <td>{paymentYear.payment.toLocaleString()} kr.</td>
                        <td>{paymentYear.repayment.toLocaleString()} kr.</td>
                        <td className="hidden-xs">{paymentYear.interestRate.toLocaleString()} kr.</td>
                        <td className="hidden-xs">{paymentYear.contribution.toLocaleString()} kr.</td>
                        <td className="visible-xs">{(paymentYear.interestRate + paymentYear.contribution).toLocaleString()} kr.</td>
                        <td>{paymentYear.loanLeft.toLocaleString()} kr.</td>
                    </tr>);
            });
        }

        return (
            <div className="row" id="loanDetailedResults">
                <div className={"col-lg-8 col-md-10 col-md-offset-1 information-box " + (this.props.specificationCalculation === undefined ? "information-box-disabled" : "")}>
                    <div className="row">
                        <div className="col-md-offset-1 col-md-11">
                            <h4>Detaljeret resultat</h4>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-sm-12">
                            <table className="table table-striped table-condensed table-responsive">
                                <thead>
                                    <tr>
                                        <th>År</th>
                                        <th>Ydelse</th>
                                        <th>Afdrag</th>
                                        <th className="hidden-xs">Rente</th>
                                        <th className="hidden-xs">Bidrag</th>
                                        <th className="visible-xs">Rente+Bidrag</th>
                                        <th>Restgæld</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {detailedResultRows}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
});

var LoanApplication = React.createClass({
    getInitialState: function () {
        return {
            information: {
                company: undefined,
                amount: undefined,
                value: undefined,
                numberOfLoans: 1,
                hasAllInformationBeenGiven: false
            },
            specification: [],
            configuration: {
                defaultSettings: [],
                companies: []
            }
        };
    },
    componentWillMount: function () {
        var xhr = new XMLHttpRequest();
        xhr.open("get", "/Home/LoadData", true);
        xhr.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
        xhr.onload = function () {
            var receivedData = JSON.parse(xhr.responseText);
            this.setState({ configuration: receivedData });
        }.bind(this);
        xhr.send();
    },
    setSpecificationCalculation: function (loanInformation, loanSpecification) {
        if (loanInformation === undefined || loanSpecification === undefined || !loanInformation.hasAllInformationBeenGiven)
            return;

        var specification = [];
        for (var index = 0; index < loanSpecification.length; index++) {
            specification.push({
                percentage: loanSpecification[index].percentage,
                type: loanSpecification[index].type,
                period: loanSpecification[index].period,
                interestRate: loanSpecification[index].interestRate
            });
        }

        var sendData = JSON.stringify({
            information: {
                company: loanInformation.company,
                amount: loanInformation.amount,
                value: loanInformation.value,
                numberOfLoans: loanInformation.numberOfLoans
            },
            specification: specification
        });

        var xhr = new XMLHttpRequest();
        xhr.open("post", "/Home/CalculateSpecification", true);
        xhr.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
        xhr.onload = function () {
            var receivedData = JSON.parse(xhr.responseText);
            this.setState({ specificationCalculation: receivedData });
        }.bind(this);
        xhr.send(sendData);
    },
    handleInformationChange: function (loanInformation) {
        this.setState({ information: loanInformation });

        this.setSpecificationCalculation(loanInformation, this.state.specification);
    },
    handleSpecificationChange: function (loanSpecification) {
        this.setState({ specification: loanSpecification });

        this.setSpecificationCalculation(this.state.information, loanSpecification);
    },
    render: function () {
        return (
            <div>
                <LoanHeader />
                <LoanInformation loanConfiguration={this.state.configuration} loanInformation={this.state.information} onInformationChange={this.handleInformationChange} />
                <LoanInput loanConfiguration={this.state.configuration} loanInformation={this.state.information} loanSpecification={this.state.specification} onSpecificationChange={this.handleSpecificationChange} />
                <LoanResultsOverview specificationCalculation={this.state.specificationCalculation} />
                <LoanResultsDetailed specificationCalculation={this.state.specificationCalculation} />
            </div>
        );
    }
});

ReactDOM.render(
    <LoanApplication />,
    document.getElementById("content")
);